.Container {
    display: flex;
    margin: 0;
    padding: 0;
}

.Label {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 18px;
    font-weight: 500;
    color: #303086;
    letter-spacing: 0.05em;
    line-height: 1.5em;
  }
  
.Input {
  display: block;
  width: 200px;
  margin-bottom: 30px;
  margin-top: 10px;
  font-size: 14px;
  font-weight: 500;
  color: #303086;
  letter-spacing: 0.05em;
  line-height: 1.5em;
  border: 1px solid #303086;
  border-radius: 5px;
  padding-left: 10px;
}